.course-form a:hover .img-wrapper:before {
    transition: all 0.2s ease-out;
    content: '';
    position: absolute;
    top: 0;
    background: black;
    width: calc(100% - 9px);
    height: calc(100% - 9px);
    z-index: 10;
    opacity: 0.6;
}

.course-form a:hover .img-wrapper:after {
    transition: all 0.2s ease-out;
    top: calc(55px - 25px);
    left: calc(50% - 67px);
    content: 'Preview';
    position: absolute;
    z-index: 20;
    border: 3px solid #EF9B2B;
